import React from 'react';

const AppFooter = () => (
  <div className="app-footer">
    <p>©2017</p>
    <a
      href="https://github.com/eclipseeer"
      rel="noreferrer noopener"
      target="_blank"
    >github.com/eclipseeer</a>
  </div>
);

export default AppFooter;



